.b3dviewer-multiple-model .components-panel__row.width-100 input {
  width: 220px;
}
.b3dviewer-multiple-model .btn-dismiss {
  height: auto;
  margin-top: 3px;
}
#closeBtn {
  right: 20px;
  top: 65px;
  &.sidebar-open {
    right: 300px;
    top: 62px;
  }
}
[data-align="center"] .aligncenter {
  margin: 0 auto;
}
.modelPanel.is-opened {
  border: 1px solid #ddd;
}
.modelRemover {
  justify-content: end;
  button.components-button {
    background: rgb(168, 20, 20);
    &:hover:not(:disabled) {
      background: rgb(117, 13, 13);
    }
  }
}
.b3dviewer.woocommerce {
  width: 600px;
  margin: 0 auto;
}
.woocommerce model-viewer {
  border: 1px solid #ddd;
}
.modelViewerIsSelected {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 999;
}
.b3dviewer_front_shortcode .components-snackbar {
  position: fixed;
  top: 130px;
  right: 20px;
}
.b3dviewer_front_shortcode {
  margin: 0 auto;
}
.b3dviewer-tab-panel [role="tablist"] {
  display: flex;
  justify-content: space-around;
  button {
    flex: 1;
    justify-content: center;
    &[aria-selected="true"] {
      background: var(--wp-admin-theme-color);
      color: #fff;
    }
  }
}
.bp_model_parent {
  position: relative;
  .DMC {
    pointer-events: none;
  }
  .setDefaultViewContainer {
    position: absolute;
    right: 10px;
    top: 10px;
    display: flex;
    gap: 10px;
    button {
      color: #fff !important;
      font-weight: 600;
    }
  }
}
.deviceForUnit {
  .iconButton {
    height: 30px;
    width: 24px;
    padding: 0;
    border: 1px solid #ddd;
    outline: none;
  }
  .bDevicePopup {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    position: absolute;
    top: -15px;
    z-index: 999;
  }
  .bDevice {
    position: relative;
    width: 30px;
    display: flex;
    margin-top: -10px;
  }
}
.block-editor-block-inspector .b3dviewer-tab-panel .components-toggle-control {
  margin-bottom: 10px;
}
.bp3d_help {
  margin-top: 6px;
  font-weight: 600;
}
